footer {
    position: absolute;
    bottom: 2em;
    margin-left: 10px;
    color: #f2f2f2;
    font-family: cursive;
}

.nav-title {
    font-size: 24px;
    font-weight: 700;
    display: inline-block;
    color: #8896ff;
    padding-left: 10px;
    font-family: cursive;
}

.nav {
  overflow: hidden;
  background-color: #101085;
  height: 100vh;
}

.nav-link {
  float: left;
  color: #f2f2f2;
  background-color: #101085;
  text-align: left;
  padding: 24px 10px;
  text-decoration: none;
  font-size: 15px;
  width: 100%;
  border:none;
  cursor: pointer;
  outline:0;
}

.nav-link:hover {
  background-color: #2068aa;
}

audio {
    position: absolute;
    bottom: 2em;
    margin-left: 10px;
}

body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin: 0px;
    background-color: lavender;
    background-image: url(/static/media/projectexample.21568cb0.png);
}

.alert {
    background-color: #900090;
    color: #ffffff;
    margin: 50px;
}

form {
    background-color: #aaccff;
    width: 30%;
    height: 250px;
    margin: 20px auto;
    padding: 25px;
    border-radius: 2px;
}

input {
    margin: 5px;
    margin-bottom: 10px;
}

.log-in, .log-out, .register {
  background-color: #2068aa;
  border-radius: 8px;
  color: #f8f8f8;
  min-height: 40px;
  min-width: 80px;
  outline:0;
  cursor: pointer;
}

.log-in:hover, .log-out:hover, .register:hover {
  background-color: #8896ff;
}

.link-button {
    background:none;
    color:inherit;
    border:none;
    padding:0;
    font: inherit;
    outline:0;
    border-bottom:1px solid #402040; 
    cursor: pointer;
}
 .welcome {
     font-family: fantasy;
     color: #402040;
     text-align: center;
 }
